.pickle-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(20, 80, 34);
}

.serve-number {
  color: rgb(157, 157, 157);
}

.top-row-position-options {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.point-dropdown {
  display: none;
}

.content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background-color: rgb(27, 60, 107);
  background: radial-gradient(
    circle,
    rgba(47, 105, 166, 1) 0%,
    rgba(0, 27, 60, 1) 100%
  );
}

.teams-or-single {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

button {
  /* width: 100%; */
  font-size: 5rem;
  margin-block: 2rem;
  padding-inline: 2rem;
  cursor: pointer;
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: rgb(45, 45, 45);
}

button:hover {
  filter: contrast(0.75) brightness(1.3);
  box-shadow: 5px 5px 10px black;
}

input {
  width: 100%;
  height: 100%;
  font-size: 3rem;
  margin-block: 1rem;
  margin-inline: 1rem;
}

.flex-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.color-input {
  width: 8rem;
  height: 4rem;
}

p {
  font-size: 3rem;
  margin-inline: 1rem;
}
.confirm-start {
  width: 85%;
  height: 85vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
}

.court {
  width: 100%;
  height: 100%;

  /* display: flex;
  flex-direction: column;
  justify-content: space-between; */
  display: grid;
  grid-template-rows: 4fr 3fr 4fr;
}

.right-court,
.left-court {
  display: flex;
  /* height: 100%; */
  /* flex-direction: column; */
  /* width: 100%; */
}

.quads {
  border: solid 5px white;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background-color: rgb(102, 143, 102);
}

.kitchen {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  border: 5px solid rgb(226, 132, 1);
  background-color: rgb(98, 116, 77);
  box-shadow: 0 0 90px rgba(0, 0, 0, 0.3) inset;
}
.net {
  width: 100%;
  height: 2px;
  background-color: white;
  border-bottom: 2px solid black;
  border-style: dashed;
  box-shadow: 10px 0 10px rgba(0, 0, 0, 0.538);
}

.top-row {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;

  width: 100%;
}
.top-row button {
  width: auto;
}

.game-stats {
  /* justify-content: center; */
  /* padding-top: 1rem; */
  font-size: 6rem;
  font-weight: 900;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
    8px 8px 0 #000;
}

.score {
  display: inline-flex;
}
.right-side {
  flex-direction: row-reverse;
}

.players {
  top: 0;
  left: 0;
  width: 150px;
  height: 150px;
  position: absolute;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  font-weight: bold;
  color: black;
  text-shadow: 2px 2px 0 white, -2px -2px 0 white, 2px -2px 0 white,
    -2px 2px 0 white;
  transition: all 500ms ease-out;
  box-shadow: 5px 5px 5px black;
}

.first-letter {
  font-size: 6em;
}

.serving {
  animation-name: borderColor;
  animation-duration: 1000ms;
  animation-iteration-count: infinite;
  box-shadow: 0 0 10px rgb(255, 255, 255);
}

@keyframes borderColor {
  0% {
    border: 3px solid rgba(255, 255, 255, 0);
    box-shadow: 0 0 10px 10px rgba(251, 251, 251, 0);
  }
  50% {
    border: 3px solid rgb(255, 255, 255);
    box-shadow: 0 0 10px 10px rgb(255, 255, 255);
  }
  100% {
    border: 3px solid rgba(255, 255, 255, 0);
    box-shadow: 0 0 10px 10px rgba(252, 252, 252, 0);
  }
}

.green-button,
.red-button {
  position: absolute;
  transition: all 1s;
  height: 9rem;
}
.green-button {
  background-color: rgb(0, 124, 0);
}
.red-button {
  background-color: rgb(218, 0, 0);
}

.button-on-right {
  left: 80%;
}

.button-on-left {
  left: 2%;
}

.play-ball {
  /* width: 35%; */
  background-color: rgb(14, 98, 14);
  color: white;
  padding: 1rem;
}
.switch-sides {
  font-size: 3rem;
  /* position: absolute; */
  left: 1%;
}

.switch-server {
  font-size: 3rem;
  /* position: absolute; */
  right: 1%;
}

.switch-players-left,
.switch-players-right {
  font-size: 2rem;
  width: 10%;
  position: absolute;
  top: 55%;
  background-color: rgb(38, 38, 90);
  color: white;
}

.switch-players-left {
  left: 1%;
}

.switch-players-right {
  right: 1%;
}

.switch-players-right::before,
.switch-players-left::before {
  content: "\2191";
  font-size: 4rem;
  transform: translateY(-0.5rem);
}

.switch-players-right::after,
.switch-players-left::after {
  content: "\2193";
  font-size: 4rem;
  transform: translateY(-0.2rem);
}

.game-over-congrats {
  width: 90%;
  height: 90%;
  background-color: rgb(14, 53, 30);
  border-radius: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-shadow: 0 0 30px 15px rgba(0, 0, 0, 0.54);
}
h1 {
  font-size: 8rem;
}

.quad-1 {
  box-shadow: 60px 60px 90px rgba(0, 0, 0, 0.3) inset;
}

.quad-2 {
  box-shadow: 60px -60px 90px rgba(0, 0, 0, 0.3) inset;
}

.quad-3 {
  box-shadow: -60px 60px 90px rgba(0, 0, 0, 0.3) inset;
}

.quad-4 {
  box-shadow: -60px -60px 90px rgba(0, 0, 0, 0.3) inset;
}

.a-winner-is-you {
  font-size: 6rem;
  font-weight: 900;
}

.name-color {
  display: flex;
  justify-content: center;
}

.play-up-to {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  top: 30%;
  background-color: rgb(45, 45, 45);
  color: white;
  border-radius: 1rem;
  border-top: 2px solid grey;
  border-left: 2px solid grey;
  border-bottom: 2px solid rgb(32, 32, 32);
  border-right: 2px solid rgb(32, 32, 32);
}

h2 {
  font-size: 32px;
  font-weight: bold;
}

.play-up-to input {
  width: 5rem;
}

