}

@media only screen and (max-width: 400px) {
  .color-input {
    display: none;
  }
  .players {
    width: 50px;
    height: 50px;
    transform: translate(-1rem, 0.5rem);
    font-size: 1rem;
  }
  .first-letter + span {
    display: none;
  }
  .swith-players-text {
    display: none;
  }
  .switch-players-left,
  .switch-players-right {
    padding: 1.4rem;
    transform: translate(0, 0.7rem);
  }

  .switch-players-right::before,
  .switch-players-left::before {
    content: "\2191";
    font-size: 2rem;
