    flex-direction: row;
    transform: translate(0);
    margin-left: -1.5rem;
  }
  .game-stats {
    font-size: 3rem;
    font-weight: 900;
    transform: translateY(-1.5rem);
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
      8px 8px 0 #000;
  }
  .number-input {
    display: none;
  }
  .point-dropdown {
    display: inline;
    font-size: 1.5rem;
  }
}
